@import 'variables';

.medications-form { // The table element
    padding: 1rem 2rem;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;

    .medication {
        .main-area {
            display: flex;
            gap: 0.5rem;
            align-items: center;
        }

        h3 {
            color: $pastel-blue;
            text-align: center;
            min-width: 10rem;
            width: 13rem;
        }

        .expanded-area {
            display: flex;
            flex-direction: column;
            gap: 0.5rem;
            $columns: 1fr 2fr 2fr 2fr 2fr 1fr 1fr 1fr 1fr 2fr;

            .headings {
                display: grid;
                grid-template-columns: $columns;
                gap: 1rem;
                align-items: center;
                justify-items: center;
                padding: 1rem 0 0.25rem 0;
                
                p {
                    font-weight: bold;
                    text-align: center;
                }
            }
            
            .adult-data, .pediatric-data {
                display: flex;
                flex-direction: column;
                background: #f5f5f5;
                padding: 0.5rem;
                gap: 0.5rem;
                
                .basic-info {
                    display: grid;
                    grid-template-columns: $columns;
                    gap: 0.5rem;
                    align-items: center;

                    input[type=text],
                    input[type=number] {
                        width: 100%;
                    }

                    input[type=checkbox] {
                        margin: 0 auto;
                    }

                    p {
                        font-weight: bold;
                        text-decoration: underline;
                    }
                }
            }

            .administration-routes {
                display: flex;
                gap: 1rem;
                align-items: center;

                p {
                    font-weight: bold;
                }
    
                label {
                    display: flex;
                    gap: 0.25rem;
                    align-items: center;
                }
            }
        }
    }

    .expand-bttn {
        border: none;
        display: flex;
        align-items: center;

        svg {
            height: 1rem;
            width: 1rem;
        }
    }

    .expand-bttn.expanded {
        background-color: #ddd;

        &:hover {
            background-color: #999;
        }
    }

    .expanded-area.collapsed {
        display: none;
    }

    .delete-bttn {
        all: unset;
        background-color: #ddd;
        padding: 0 1rem;
        max-width: min-content;
        cursor: pointer;

        &:hover {
            background-color: aquamarine;
        }
    }

    button {
        margin: 0;
        align-self: flex-start;
    }
}