@import 'variables';

.mdb-form-wrapper {
    padding: 1rem;
    display: flex;
    flex-direction: column;

    .hidden { display: none !important; }

    label {
        display: flex;
        gap: 0.5rem;
        align-items: center;
        white-space: nowrap;
    }
    

    .problem-wrapper {
        display: flex;
        flex-direction: column;
        padding: 1rem 0;

        label {
            max-width: 30vw;
    
            input[type="text"] {
                width: 100%;
            }
        }

        .solutions {
            display: flex;
            flex-direction: column;

            .solution {
                .solution-header {
                    display: flex;
                    gap: 1rem;
                    align-items: center;
                    flex-wrap: wrap;

                    h2 {
                        font-weight: bold;
                    }

                    .remove-solution-btn {
                        margin-left: auto;
                    }
                }

                .solution-content {
                    margin: 0.5rem 0;

                    .actions, .contraindications {
                        display: flex;
                        flex-direction: column;
                        gap: 0.5rem;
                    }
                    
                    .solution-tab-selector {
                        margin: 0.5rem 0;
                        display: flex;
                        gap: 0.5rem;

                        span {
                            display: inline-block;
                            border: 1px solid #333;
                            border-radius: 0.25rem 0 0.25rem 0;
                            border-style: solid;
                            padding: 0.5rem;
                            cursor: pointer;
                            
                            &:hover {
                                background-color: aliceblue;
                            }
                        }
                    }

                    .content-wrapper {
                        padding: 0.5rem;
                        border: 1px solid #333;
                        border-radius: 0 0.5rem 0 0.5rem;

                        .mdb-block-heading {
                            font-weight: bold;
                        }

                        .inputs {
                            display: flex;
                            flex-wrap: wrap;
                            gap: 1rem;

                            label {
                                position: relative;
                            }

                            button {
                                padding: 0.1rem 0.75rem;
                            }
                        }
                    }
                }
            }
        }

        .layer {
            display: flex;
            background-color: lavender;
            padding: 1rem;
            margin: 0.15rem 0;
            border-radius: 0.5rem;

            .problem-content-heading {
                font-weight: bold;
            }
            
            .problem-content-block {
                flex: 1 1 100%;
                display: flex;
                flex-direction: column;
                gap: 0.25rem;

                p {
                    margin-bottom: 0.25rem;
                }

                label {
                    position: relative;
                }

                .mdb-block-item {
                    button {
                        padding: 0.1rem 0.75rem;
                    }
                }

                select {
                    padding: 0.15rem;
                }
            }

            .problem-notes {
                flex-basis: 60%;
            }
        }

        .problem-meta-info {
            display: flex;
            flex: 0 1 100%;
            flex-flow: row wrap;
            align-items: center;
            gap: 0.5rem;

            .problem-heading {
                font-weight: bold;
            }
        }
    }

    button {
        border: 1px solid #ddd;
        align-self: start;

        &:hover {
            background-color: #ddd;
        }
    }

    #submit-bttn {
        margin-left: auto;
        background-color: $light-blue;
    }

    .search-results {
        position: absolute;
        top: 100%;
        border: 1px solid #333;
        border-radius: 0.25rem;
        background-color: #fff;
        max-height: 10rem;
        width: 30vw;
        overflow: auto;
        z-index: 999;

        p {
            padding: 0.15rem 0.5rem;
            cursor: pointer;

            b {
                font-weight: bold;
                pointer-events: none;
            }

            &:hover {
                background-color: #ddd;
            }
        }
    }
}

/**********************************
 *                                *
 *      PROBLEM ASSOCIATIONS      *
 *                                *
 **********************************/

#associations-wrapper {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;

    #associations-list {
        display: flex;
        flex-direction: column;
        gap: 1rem;

        .associations-and,
        .associations-or {
            display: flex;
            flex-direction: column;
            gap: 5px;
            align-items: flex-start;

            select, label{
                margin-left: 1rem;
            }

            label {
                display: flex;
                gap: 0.5rem;
            }
        }
    }

    #associations-submit {
        background-color: $light-blue;
        margin-top: 1rem;
        border-color: #ddd;
        color: #fff;
    }
}