@import 'variables';

.modal {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
    z-index: 1;
    overflow: auto;

    p {
        padding-top: 7px;
        padding-bottom: 7px;
    }
    ul {
        list-style: inside;
    }
    &.hidden {
        display: none;
    }

    .modal-dialog {
        margin: 15% auto; /* 15% from the top and centered */
        // padding: 20px;
        border: 1px solid #888;
        background-color: white;
        width: 80%; /* Could be more or less, depending on screen size */

        .modal-header {
            font-size: larger;
            background-color: $header-color;
            width: 100%;

            .close-button {
                float: right;
                padding: 1px;
            }
            .modal-title {
                color: white;
                text-align: center;
                margin-left: auto;
                width: 100%;
            }
            .modal-body {
                padding: 40px;
            }
        }
    }
}


.scenario-form-wrapper {
    border-top: 1px solid #222;
    width: 100%;
    height: 100% -58px - 48px; //subtract header and tab heights
    overflow-y: scroll;

    .scenario-form {
        padding: 0.5rem;

        &.scenario-view-hidden {
                    display: none !important;
            }

        .save-changes {
            float: right;
        }

        .info-wrapper {
            h1 {
                color: $pastel-blue;
            }
        }

        #basic-info {
            display: flex;
            flex-direction: column;
            gap: 0.5rem;

            h1 {
                color: $pastel-blue;
            }

            label {
                display: flex;
                flex-direction: column;
                font-weight: 500;
            }
        }

        #sample,
        #opqrst {
            display: flex;
            flex-direction: column;
            gap: 0.5rem;

            label {
                display: flex;

                input {
                    display: inline-block;
                    margin: 0 1rem;
                    width: 100%;
                }
            }
        }

        #medications {
            // display: flex;
            // flex-direction: column;
            // gap: 0.5rem;

            .meds-wrapper {
                // display: grid;
                // grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
                // gap: 0.5rem;
                table {
                    width: 100%;
                    th {
                        font-weight: bold;
                    }
                    thead {
                        th {
                            text-align: center;
                        }
                    }
                    tbody {
                        input {
                            width: 100%;
                        }
                    }

                }

                label {
                    // display: flex;
                    // gap: 1em;
                    border: 1px solid #ddd;
                    border-style: none none solid none;
                    padding: 0.5em;

                    input {
                        max-width: 15em;
                        margin-left: auto;
                        margin-right: 3em;
                    }
                }
            }
        }

        .vitals {
            // display: grid;
            // grid-template-columns: repeat(auto-fill, minmax(30rem, 1fr));
            // gap: 1rem;
            table {
                width: 100%;
                 th {
                        font-weight: bold;
                    }
                thead {
                    th {
                        text-align: center;
                    }
                }
                tbody {
                    input {
                        width: 100%;
                    }
                }
            }
            .vital-wrapper {
                border: 1px solid $header-color;
                border-radius: 0.5rem;
                padding: 0.5rem;
                max-width: calc(100vw - 1rem);

                h1 {
                    color: $pastel-blue;
                }

                .vitals-box {
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    gap: 1rem;

                    label {
                        display: flex;
                        font-weight: 500;

                        input {
                            display: inline-block;
                            margin: 0 0 0 1rem;
                            width: 100%;
                        }
                    }
                }
            }
        }

        #scenario-steps {
            display: flex;
            flex-direction: column;

            h1 {
                color: $pastel-blue;
            }

            .header-item {
                h2 {
                    color: $pastel-blue;
                }
            }

            .step-item {
                border: 1px solid #333;
                border-style: none none solid none;
                padding: 0.5rem;

                &:nth-child(odd) {
                    background-color: #eee;
                }

                label {
                    display: flex;
                    align-items: center;

                    input {
                        display: inline-block;
                        max-height: min-content;
                        align-self: flex-end;
                        margin-left: 1em;
                    }
                }
            }
        }
    }
       #submit-next {
            background-color: $pastel-blue;
            padding: 0.75em 1.25em;
            color: #ddd;
            margin-left: auto;
            cursor: pointer;
        }
}

#tab-selector {
    display: flex;
    flex-flow: row wrap;
    background-color: $header-color;

    button {
        all: unset;
    }

    .active-tab {
        background-color: $header-dark;
    }

    .tab-button {
        color: #ddd;
        cursor: pointer;
        padding: 0.75em 1.25em;

        &:hover {
            background-color: $header-dark;
        }
    }
}

.hidden {
    display: none;
}

.error {
    color: red;
}
